*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

address {
  font-style: normal;
}

img {
  min-height: auto;
  max-width: 100%;
  object-fit: cover;
  display: block;
}

.container {
  max-width: 1136px;
  margin: 0 auto;
}

@media (max-width: 767px) {
  .container {
    width: 375px;
    margin: 0 auto;
  }
}

@media (min-width: 768px) and (max-width: 1199px) {
  .container {
    width: 768px;
    margin: 0 auto;
  }
}

:root {
  --black: #111;
  --black-pearl: #1e1823;
  --orange: #fd9222;
  --pastel-orange: #fcf1e0;
  --white: #fff;
  --black-gradient1: rgba(17, 17, 17, 0);
  --black-gradient2: rgba(17, 17, 17, .4);
  --animation: 250ms cubic-bezier(.4, 0, .2, 1);
}

header, section {
  margin: 0;
  padding: 0;
}

a {
  cursor: pointer;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

.header-container {
  width: 1136px;
  height: 80px;
  z-index: 100;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 0 0 10px 10px;
  align-items: center;
  gap: 188px;
  display: flex;
  position: fixed;
  top: -40px;
  left: 50%;
  transform: translate(-50%, 50%);
}

.header-logo-container {
  cursor: pointer;
  justify-content: flex-start;
  align-items: center;
  gap: 4px;
  display: flex;
}

.header-logo {
  color: #111;
  letter-spacing: -.54px;
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 20px;
}

.header-logo span {
  color: #111;
  letter-spacing: -.54px;
  font-family: Montserrat;
  font-size: 18px;
  font-style: italic;
  font-weight: 700;
  line-height: 20px;
}

.icon-logo {
  width: 24px;
  height: 24px;
  stroke: var(--orange);
  fill: var(--pastel-orange);
}

.nav-container {
  justify-content: flex-start;
  align-items: center;
  gap: 137px;
  display: flex;
}

.nav-list {
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 24px;
  display: flex;
}

.nav-link {
  color: var(--black);
  letter-spacing: -.28px;
  transition: color var(--animation);
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
}

.nav-link:hover, .nav-link:focus {
  color: var(--orange);
}

.header-socials {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 18px;
  display: flex;
}

.icon-socials {
  stroke: var(--black);
  fill: none;
  transition: var(--animation);
  justify-content: center;
  align-items: center;
  display: flex;
}

.icon-socials:hover, .icon-socials:focus {
  stroke: var(--orange);
}

.header-burger-menu {
  display: none;
}

.hero {
  justify-content: center;
  align-items: center;
  padding: 80px 32px 72px;
  display: flex;
}

.hero-container {
  background: linear-gradient(180deg, var(--black-gradient1) 0%, var(--black-gradient2) 100%), url("hero-bg.102eea69.jpg"), lightgrey -70.319px -105.03px / 110.32% 139.265% no-repeat;
  width: 1136px;
  height: 600px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 30px;
  padding-top: 381px;
}

.hero-content-container {
  width: 1056px;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  margin: 0 auto;
  display: flex;
}

.hero-caption {
  width: 484px;
  height: 102px;
  color: var(--white);
  font-family: Montserrat;
  font-size: 28px;
  font-weight: 500px;
  letter-spacing: -.84px;
  line-height: 34px;
}

.hero-navigation-container {
  color: #fff;
  justify-content: center;
  align-items: center;
  gap: 552px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  display: flex;
}

.hero-buttons-container {
  align-items: center;
  gap: 14px;
  display: flex;
}

.buy-button {
  color: var(--white);
  background-color: var(--orange);
  width: 144px;
  height: 45px;
  transition: color var(--animation), background-color var(--animation);
  border-radius: 100px;
  align-items: flex-start;
  gap: 14px;
  padding: 14px 40px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  display: flex;
}

.buy-button:hover, .buy-button:focus {
  color: var(--orange);
  background-color: var(--pastel-orange);
}

.buy-button:active {
  color: var(--white);
  background-color: var(--orange);
}

.how-button {
  border: 1px solid var(--white);
  color: var(--white);
  width: 183px;
  height: 45px;
  transition: color var(--animation), border var(--animation);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 100px;
  gap: 14px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.how-button:hover, .how-button:focus {
  color: var(--orange);
  border: 1px solid var(--orange);
}

.how-button:active {
  color: var(--white);
  border: 1px solid var(--white);
}

.hero-scroll-text {
  color: #fff;
  letter-spacing: -.42px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.hero-scroll-button {
  width: 38px;
  height: 38px;
  background-color: var(--white);
  transition: background-color var(--animation);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.hero-scroll-down {
  width: 14px;
  height: 8px;
  fill: var(--orange);
  transition: fill var(--animation);
}

.hero-scroll-container {
  width: 163px;
  height: 53px;
  background: #1e1823;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  gap: 14px;
  display: flex;
}

.hero-scroll-container:hover, .hero-scroll-container:focus {
  background: none;
}

.hero-scroll-container:hover p, .hero-scroll-container:focus p {
  color: var(--orange);
}

.hero-scroll-button:hover a, .hero-scroll-button:focus a {
  background-color: var(--orange);
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 1000;
  background-color: rgba(46, 47, 66, .4);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.hero-modal {
  width: 609px;
  background: #fafafa;
  border-radius: 15px;
  padding: 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12), 0 2px 1px rgba(0, 0, 0, .2);
}

.hero-modal-close {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  padding: 0;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 24px;
  right: 24px;
}

.hero-modal-close:hover {
  fill: #fd9222;
}

.hero-modal-heading {
  color: #111;
  letter-spacing: -.72px;
  text-transform: uppercase;
  width: 298px;
  text-align: center;
  margin: 0 auto 32px;
  font-family: Montserrat;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 28px;
}

.hero-modal-heading span {
  color: #fd9222;
  letter-spacing: -.72px;
  text-transform: uppercase;
  font-family: Montserrat;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 28px;
}

.buy-now-list {
  width: 513px;
  justify-content: center;
  align-items: center;
  gap: 16px;
  margin-bottom: 40px;
  display: flex;
}

.buy-now-item {
  width: 160px;
  background: #fff;
  border: 1px solid rgba(17, 17, 17, .1);
  border-radius: 15px;
  flex-direction: column;
  flex-shrink: 0;
  justify-content: flex-end;
  align-items: center;
  gap: 14px;
  padding: 24px 20px 18px;
  display: flex;
}

.buy-now-img {
  width: 120px;
  height: 107px;
}

.buy-now-span {
  color: #111;
  background: #fafafa;
  border: 1px solid rgba(17, 17, 17, .1);
  border-radius: 100px;
  justify-content: center;
  align-items: flex-start;
  gap: 14px;
  padding: 8px 27px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  display: flex;
}

.hero-modal-form {
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 18px;
  display: flex;
}

.hero-form-group {
  border: 1.5px solid rgba(17, 17, 17, .05);
  border-radius: 13px;
  align-self: stretch;
  align-items: flex-start;
  padding: 22px 24px;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.hero-form-group:hover, .hero-form-group:focus {
  box-shadow: 0 5.445px 5.445px rgba(0, 0, 0, .03);
}

.hero-modal-input {
  width: 392px;
  height: 22px;
  background: #fafafa;
  border: none;
  flex: 1 0;
  align-self: stretch;
  align-items: center;
  gap: 21.78px;
  display: flex;
}

.hero-modal-input::-ms-input-placeholder {
  color: rgba(64, 64, 64, .3);
  letter-spacing: -.36px;
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.hero-modal-input::placeholder {
  color: rgba(64, 64, 64, .3);
  letter-spacing: -.36px;
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.hero-modal-input:hover, .hero-modal-input:focus {
  outline-style: none;
}

.hero-tel-group {
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
  display: flex;
}

.flag-number {
  width: 125px;
  border-right: 3px solid rgba(17, 17, 17, .05);
  justify-content: flex-start;
  align-items: center;
  gap: 18px;
  margin-right: 28px;
  display: flex;
}

.country-code {
  color: #000;
  letter-spacing: -.36px;
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.hero-modal-textarea {
  max-width: 513px;
  resize: none;
  background: #fafafa;
  border: 1.5px solid rgba(17, 17, 17, .05);
  border-radius: 13px;
  flex: 1 0;
  align-self: stretch;
  align-items: center;
  gap: 21.78px;
  padding: 22px 24px 93px;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.hero-modal-textarea::-ms-input-placeholder {
  color: rgba(64, 64, 64, .3);
  letter-spacing: -.36px;
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.hero-modal-textarea::placeholder {
  color: rgba(64, 64, 64, .3);
  letter-spacing: -.36px;
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.hero-modal-textarea:focus, .hero-modal-textarea:hover {
  outline-style: none;
  box-shadow: 0 5.445px 5.445px rgba(0, 0, 0, .03);
}

.hero-modal-btn {
  color: #fff;
  cursor: pointer;
  background: #fd9222;
  border: none;
  border-radius: 100px;
  align-self: flex-start;
  gap: 14px;
  padding: 14px 40px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.hero-modal-btn:hover, .hero-modal-btn:focus {
  color: #fd9222;
  background: #fcf1e0;
}

.modalform {
  flex-direction: column;
  align-items: flex-start;
  gap: 18px;
  margin-top: 8px;
  display: flex;
}

.input-container {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

.input-label {
  color: #000;
  letter-spacing: -.28px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.input-area {
  gap: 21px;
