}

.input-personalinfo-container .input-data {
  width: 246px;
}

.input-data {
  height: 66px;
  border: 1.5px solid rgba(17, 17, 17, .05);
  border-radius: 13px;
  flex: 1 0;
  align-self: stretch;
  align-items: center;
  gap: 21.78px;
  padding: 22px 24px;
  display: flex;
}

.input-data::-ms-input-placeholder, .tel-number::-ms-input-placeholder, .user-comment::-ms-input-placeholder {
  color: rgba(64, 64, 64, .3);
  letter-spacing: -.36px;
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.input-data::placeholder, .tel-number::placeholder, .user-comment::placeholder {
  color: rgba(64, 64, 64, .3);
  letter-spacing: -.36px;
  font-family: Montserrat;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.input-email-container, .input-card-container, .input-personalinfo-container {
  width: 509px;
}

.flag-code {
  width: 125px;
  height: 32px;
  flex-direction: row;
  align-items: center;
  gap: 18px;
  display: flex;
  box-shadow: 2.722px 0 rgba(0, 0, 0, .1);
}

.flag {
  width: 22px;
}

.flag-icon {
  justify-content: center;
  align-items: center;
  display: flex;
}

.tel-number {
  width: 237px;
